import React, { useEffect, useRef, useState } from "react";
import { useDispatch, useSelector } from "react-redux";
import { AppDispatch, RootState } from "@/redux/app/store";
import { fetchServices } from "../../redux/features/ServicesSlice";
import { ChevronLeft, ChevronRight } from "lucide-react";

const Bookinglist = () => {
  const dispatch: AppDispatch = useDispatch();
  const { service, loading, error } = useSelector((state: RootState) => state.service);
  const scrollRef = useRef<HTMLDivElement | null>(null);

  useEffect(() => {
    dispatch(fetchServices());
  }, [dispatch]);

  const scroll = (direction: "left" | "right") => {
    const current = scrollRef.current;
    if (current) {
      const scrollAmount = 300;
      current.scrollBy({
        left: direction === "left" ? -scrollAmount : scrollAmount,
        behavior: "smooth",
      });
    }
  };

  return (
    <div className="text-white px-8 py-12">
      <h2 className="text-3xl font-bold text-[#D6D7D6]">Our Services</h2>
      <p className="text-gray-400 mt-2 mb-6">
        Check out our top-class haircuts, beard trims, and more.
      </p>

      <div className="relative group">
        {/* Left shadow */}
        <div className="absolute left-0 top-0 h-full z-30 w-[60px] bg-gradient-to-r from-[#1B1B1A] via-[#1B1B1A]/80 to-transparent pointer-events-none" />

        {/* Left Button */}
        <button
          onClick={() => scroll("left")}
          className="absolute z-40 left-0 top-1/2 transform -translate-y-1/2 bg-black/40 hover:bg-black/60 text-white p-2 rounded-full opacity-0 group-hover:opacity-100 transition-opacity duration-300"
        >
          <ChevronLeft size={22} />
        </button>

        {/* Scrollable Cards */}
        <div
          ref={scrollRef}
          className="overflow-x-auto whitespace-nowrap flex gap-4 px-2 py-4 scroll-smooth"
        >
          {loading ? (
            <p className="text-gray-400">Loading...</p>
          ) : error ? (
            <p className="text-red-500">Error fetching services</p>
          ) : (
            service.map((item: any, index: number) => (
              <div
                key={index}
                className="w-[250px] h-[200px] bg-white/5 border border-[#757442]/30 rounded-xl flex-shrink-0 p-4 cursor-pointer hover:scale-105 transition-transform duration-300 backdrop-blur-md"
              >
                <img
                  src={item.image}
                  alt={item.title}
                  className="w-full h-[110px] object-cover rounded-md mb-3"
                />
                <h4 className="text-lg font-semibold text-[#D6D7D6]">{item.title}</h4>
                <p className="text-sm text-gray-400 mt-1">â‚¹ {item.price}</p>
              </div>
            ))
          )}
        </div>

        {/* Right Button */}
        <button
          onClick={() => scroll("right")}
          className="absolute z-40 right-0 top-1/2 transform -translate-y-1/2 bg-black/40 hover:bg-black/60 text-white p-2 rounded-full opacity-0 group-hover:opacity-100 transition-opacity duration-300"
        >
          <ChevronRight size={22} />
        </button>

        {/* Right shadow */}
        <div className="absolute right-0 top-0 h-full z-30 w-[60px] bg-gradient-to-l from-[#1B1B1A] via-[#1B1B1A]/80 to-transparent pointer-events-none" />
      </div>
    </div>
  );
};

export default Bookinglist;
